import React, { useContext } from "react";

export default function Loading() {
    return(
        <div className=" backdropBlur  position-fixed container-fluid top-0 start-0 bg-dark bg-opacity-75" 
        style={{zIndex: '100000', height: '100vh'}}>
            <div className="loadingDotGroup position-fixed d-flex flex-row top-50 start-50 translate-middle">
                <div className="loadingDot"></div>
                <div className="loadingDot"></div>
                <div className="loadingDot"></div>
            </div>
        </div>
    )
}